  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

